import React from 'react';
import './styles/styles-sizes.css';
import './home.css';
import Reel from './video/reel.mp4';

const Home = () => {

    return(
        <>
            <div className="altura">
                <h1 style={{textAlign:'center'}}>Home</h1>
                <video autoPlay loop muted>
                    <source src={Reel} type="video/mp4"/>
                </video>
            </div>
        </>
    )

}

export default Home;