import { Height } from '@material-ui/icons';
import React from 'react';
import logo from '../img/logo.jpg';
import {Nav, Bars, NavMenu, NavLink, NavBtn, NavBtnLink} from './NavbarElements';


const Navbar = () => {
    return(
        <>
        <Nav>
        {/* <NavLink to="/">
              
        </NavLink> */}
          <Bars/>
          <NavMenu>
              <NavLink to="/" activeStyle>
                  Home
              </NavLink>
              <NavLink to="/" activeStyle>
                  Galeria
              </NavLink>
              <NavLink to="/" activeStyle>
                  <a href="" className="logo"><img className="logo" src={logo} alt="" style={{width: '80px', Height:'10px'}}></img></a>
                  {/* Logo */}
              </NavLink>
              <NavLink to="/" activeStyle>
                  Contacto
              </NavLink>
              <NavLink to="/" activeStyle>
                  Redes
              </NavLink>
        </NavMenu>
    </Nav>
        </>
    );
};

export default Navbar;